* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  padding: 72px 15px 15px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  height: 55px;
  width: 100%;
  background-color: #3d3f9e;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.input {
  height: 32px;
  width: 250px;
  border: none;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  padding-left: 10px;
}

.search-btn {
  height: 32px;
  width: 32px;
  border: none;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 15px;
  display: flex;
}

.photo-card {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  flex-basis: calc(25% - 11.25px);
  box-shadow: 0 2px rgba(40, 40, 40, .3);
}

.photo {
  width: 100%;
}

.info {
  justify-content: space-around;
  font-size: 12px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  height: 35px;
  width: 100px;
  color: #fff;
  background-color: #3d3f9e;
  border: none;
  border-radius: 3px;
  margin: 0 auto;
  display: block;
}

.is-hidden {
  display: none;
}

